#home {
  position: relative;
  background: url('../../assets/Images/bgIMG.png');
  background-size: cover; //standard for img as bg
  background-repeat: repeat;
  background-position: center;

  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;

  padding: 10rem 2rem 0;

  //Extra large devices
  @media screen and (min-width: 2000px) {
    padding-top:10rem;
  }

  //Screens smaller than tablet
  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }

  //Mobile
  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;

  margin: 0 2rem;

  //Extra large devices
  @media screen and (max-width: 2000px){
    width: 100%;
    margin-right: 0rem;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  .badge-cmp, .tag-cmp {
    padding: 1rem 2rem;
    background: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: auto;

    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  .tag-cmp {
    flex-direction: column;
    margin-top: 3rem;

    p {
      width: 100%;
      text-transform: uppercase;
      text-align: left;
    }
  }
  span {
    font-size: 2.5rem;

    //Extra large devices
    @media screen and (min-width: 2000px) {
      font-size: 5rem;
    }
  }

  //Screens smaller than tablet
    @media screen and (max-width: 1200px){
      justify-content: flex-start;
      align-items: flex-start;
    }
  }
.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 1rem;

  div {
    height: 100px;
    width: 100px;
    border-radius: 50%;

    background: var(--white-color);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

    img {
      width: 60%;
      height: 60%;
    }
  }
  div:nth-child(1) {
    width: 100px;
    height: 100px;
  }

  div:nth-child(2) {
    margin: 1.75rem;
    width: 150px;
    height: 150px;
  }

  div:nth-child(3) {
    width: 80px;
    height: 80px;
  }

  //Extra large devices
  @media screen and (min-width: 2000px) {
    div:nth-child(1) {
      width: 400px;
      height: 400px;
    }

    div:nth-child(2) {
      margin: 1.75rem;
      width: 300px;
      height: 300px;
    }

    div:nth-child(3) {
      width: 160px;
      height: 160px;
    }
  }
  //Screens smaller than tablet
  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;

    div {
      margin: 1rem;
    }
  }
}

.app__header-img {
  flex: 1;


  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;

  img {
    width: 100%;
    object-fit: contain;
    z-index: 1;
    height: 600px;

    //Extra large devices
    @media screen and (min-width: 2000px) {
      height: 1000px;
    }

    //Screens smaller than tablet
    @media screen and (max-width: 450px) {
      height: 450px
    }
  }

  .overlay_circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 100%
  }

  //Screens smaller than tablet
  @media screen and (max-width: 1200px) {
    margin: 2rem 0;
  }
}